import React from 'react';
import { connect } from 'react-redux';

import Header from './../header';
import Nav from './../nav';
import Banner from './../banner';
import Anav from './../Anav';
import AbizDetail from './../AbizDetail';
import AbizInfo from './../AbizInfo';
import AbizContant from './../AbizContant';
import AbizCooperate from './../AbizCooperate';
import ToTop from './../toTop';
import s from './Abiz';

function Abiz(data) {
    return (
        <div className={s.box}>
            <Header data={data.data.forecast} />
            <Nav />
            <Banner />
            <Anav router={data.route.path} />
            <AbizDetail />
            <AbizInfo />
            <AbizContant data={data.data.contant} />
            <AbizCooperate data={data.data.cooperate} />
            <ToTop />
        </div>
    );
}

export default connect(
    state => ({
        data: state,
    })
)(Abiz);




